body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.MuiFormControl-root {
	text-align: left;
	width: 194px;
}

.MuiSelect-select {
	width: 194px;
}

.input-box {
	margin: 10px auto;
}

.select-box {
	margin: 20px auto;
}
.form-box {
	width: 95%;
}

.form-paper {
	margin: 3%;
	padding: 20px;
	padding-right: 10px;
}

.send-button {
	text-align: center;
	margin: 10px;
}

.student-info {
	display: inline-block;
	margin: 5px;
}

.g-recaptcha {
	margin: 10px;
}

.g-recaptcha div {
	text-align: center;
	margin-left: auto;
	margin-right: auto;
}
#ticket-div {
	text-align: center;
	user-select: none;
	-webkit-user-select: none;
	-moz-user-select: none;
}

#ticket {
	width: 312px;
	height: 675.2px;
}

#save-button {
	display: block;
	margin: 20px auto;
}

.warning-span {
	display: inline-block;
}
@font-face {
  font-family: "gennokaku-gothic";
  src: url("/ReservationSystem/SourceHanSansJP-Medium.woff")
}

body {
  background-color: #f5f5f5;
  font-family: 'gennokaku-gothic';
}

.main {
  margin: 3%;
  margin-top: 2.5%;
}

.barcode-paper {
  width: max(300px, 30%);
  margin: auto;
  padding: 30px;
  text-align: center;
}

.banner {
  font-size: min(30px, 4.5vw) !important;
  width: 45%;
  height: 100px;
  text-align: center;
  margin: 2.5% !important;
  margin-bottom: 0;
}

.header {
  font-size: min(30px, 3.5vw);
  text-decoration: none !important;
  color: white !important;
}

#whole-paper {
  margin: 3%;
  padding: 20px 50px;
}

.appbar {
  background-color: #a00023 !important;
}
